@charset "utf-8";
/* linknav */
.linknav {
    height: 20px;
    line-height: 20px;
    margin: 10px auto;
    overflow: hidden;
    width: 1200px;
}
.breadcrumb {
    overflow: hidden;
}
.breadcrumb strong {
    font-family: Microsoft YaHei;
    font-size: 16px;
    padding: 0 7px 0 10px;
}
.breadcrumb span {
    font-family: "宋体";
}
.kefubox {
    cursor: pointer;
    float: right;
    width: 80px;
    font-size: 12px;
    color: #66646B;
    text-align: right;
    margin-right: 232px;
}
.kefubox i {
    display: inline-block;
    width: 12px;
    height: 12px;
    background: url(../img/kefu_copy.png) left top no-repeat;
    position: relative;
    right: 2px;
    top: 3px;
}
/* -----------------中间内容----------------- */
.pWrap {
    width: 1200px;
    margin: 0 auto;
}
.productIntro {
    margin-bottom: 10px;
    position: relative;
}
/* 左边商品详情 */
.pItems {
    float: left;
    width: 978px;
    background-color: #fbfbfb;
    box-shadow: 0 0 4px #c7c7c7;
    position: relative;
    z-index: 2;
}
/* 商品详情右边 */
.pItemsMain {
    float: right;
    width: 568px;
}
.pItemsName .cm {
    padding: 0 15px;
    margin: 10px 0;
    height: 60px;
    overflow: hidden;
}
.pItemsName h1 {
    float: left;
    color: #191c1f;
    font-size: 18px;
    font-family: 微软雅黑;
    line-height: 30px;
}
// .pItemsName .cm h1, .pItemsName strong {
//     font-size: 18px;
//     font-family: 微软雅黑;
//     line-height: 30px;
// }=========
.pItemsPrice {
    background-color: #fa8e19;
    height: 90px;
    margin-bottom: 10px;
    border-bottom: 3px solid #dddddd;
}
.yj {
    position: absolute;
    top: 95px;
    left: 440px;
    font-size: 12px;
    color: #fff;
    text-decoration: line-through;
}
.daojishi {
    float: right;
    padding: 10px 20px 0 0;
    color: white;
    position: absolute;
    right: 0;
}
.priceBox {
    float: left;
    overflow: hidden;
    color: white;
}
.priceBox .dt {
    float: left;
    padding: 55px 0 0 10px;
    height: 18px;
    line-height: 18px;
}
.priceBox .rmb {
    float: left;
    height: 18px;
    line-height: 18px;
    font-size: 18px;
    font-family: 微软雅黑;
    padding: 53px 0px 0px;
}
.priceBox strong {
    float: left;
    font-family: 微软雅黑;
    font-size: 50px;
    height: 45px;
    line-height: 45px;
    padding: 30px 0px 0px;
}
.p_btn {
    display: block;
    float: left;
    width: 82px;
    height: 24px;
    text-align: center;
    line-height: 24px;
    margin: 20px 0 0 15px;
    color: #df0000;
    font-size: 12px;
    background-color: #ffe400;
    position: absolute;
    top: 68px;
    right: 212px;
}
.boxWb {
    width: 5px;
    height: 96px;
    background: url(../img/productinfo.png) no-repeat -271px -121px;
    position: absolute;
    right: -5px;
}
/* 运送地址选择 */
.pItemsStock {
    margin-bottom: 10px;
    padding: 0 5px;
    position: relative;
}
.pItemsPromo .dt, .pItemsStock .dt, .pCycle_time .dt, .pCycle_note .dt {
    float: left;
    width: 90px;
    text-align: right;
    line-height: 25px;
    color: #b0b0b0;
}
.pItemsPromo .dd, .pItemsStock .dd, .pCycle_time .dd, .pCycle_note .dd {
    float: left;
    width: 465px;
    z-index: 91;
    line-height: 25px;
}
#store-selector, #store-selector_sfv, #store-selector1 {
    float: left;
    height: 26px;
    position: relative;
    z-index: 91;
}
#store-selector .text, #store-selector_sfv .text, #store-selector1 .text {
    background: #FFFFFF;
    border: 1px solid #CECBCE;
    float: left;
    height: 22px;
    line-height: 22px;
    overflow: hidden;
    padding: 0 20px 0 4px;
    position: relative;
    top: 0;
    font-size: 12px;
    font-weight: normal;
}
#store-selector .text b, #store-selector_sfv .text b, #store-selector1 .text b {
    background: url(../img/indexImg20130307.png) no-repeat -386px -2px;
    display: block;
    height: 24px;
    overflow: hidden;
    position: absolute;
    right: 0;
    top: 0;
    width: 17px;
}
.pItemsStock {
    margin-bottom: 10px;
    padding: 0 5px;
    position: relative;
    height: 25px;
    line-height: 25px;
}
.pItemsStock .dt {
    float: left;
    width: 90px;
    text-align: right;
    line-height: 25px;
    color: #b0b0b0;
}
.pItemsStock .dd {
    float: left;
    width: 465px;
    z-index: 91;
    line-height: 25px;
}
.pItemsSend {
    margin-bottom: 16px;
    padding-left: 95px;
    color: #fc8f00;
    font-size: 12px;
    line-height: 12px;
}
.pItemsChoose {
    border-top: 1px solid #efefef;
    padding-top: 10px;
    margin: 0 20px;
}
.chooseType {
    overflow: hidden;
}
.chooseBtns {
    margin: 10px 0;
    position: relative;
    height: 45px;
}
// 点击加入购物车弹出的窗口
#addcartbox{
    position: absolute;
    display: none;
    // margin: 0 auto;
}
.pWindow {
    width: 416px;
    border: 1px solid #dbdbdb;
    background-color: #ffffff;
    position: absolute;
    z-index: 999;
}
.pWindow .hd {
    border-bottom: 1px solid #196247;
    padding: 15px 10px 10px 80px;
}
#carwindow .cartItem .ct {
    color: rgb(69, 64, 58);
    font-family: 微软雅黑;
    font-size: 18px;
    margin-bottom: 5px;
    line-height: 24px;
}
#carwindow .cartItem #showcart {
    margin-bottom: 5px;
}
#carwindow .cartItem li {
    overflow: hidden;
}
.pbtn4 {
    background-color: #6c9c0a;
    height: 24px;
    line-height: 24px;
    color: white;
    float: left;
    margin-right: 10px;
    width: 90px;
    text-align: center;
}
.pWindow .pClose {
    background: url(../img/sf-stock.png) no-repeat 0 -70px;
    height: 17px;
    position: absolute;
    top: 10px;
    right: 10px;
    width: 17px;
    cursor: pointer;
}
// end点击加入购物车弹出的窗口
.pAmount {
    width: 81px;
    float: left;
    margin-right: 10px;
    overflow: hidden;
}
.pAmount span {
    float: left;
}
.pAmount .text {
    width: 53px;
    height: 43px;
    font-size: 18px;
    text-align: center;
    line-height: 43px;
    color: rgb(107, 107, 107);
    font-family: 宋体;
    border-width: 1px;
    border-style: solid;
    border-color: rgb(218, 218, 218);
    border-image: initial;
    overflow: hidden;
}
.pAmount a {
    height: 21px;
    width: 25px;
    text-align: center;
    line-height: 21px;
    display: block;
    cursor: pointer;
    color: rgb(107, 107, 107);
    font-size: 14px;
    font-family: 宋体;
    border-top: 1px solid rgb(218, 218, 218);
    border-right: 1px solid rgb(218, 218, 218);
    text-decoration: none;
    overflow: hidden;
}
.pAmount a.p-reduce {
    border-bottom: 1px solid #dadada;
}
/* +/-的disable状态 */
.pAmount a.disable {
    color: #ececec;
    cursor: default;
}
.pBtn {
    float: left;
    margin-right: 10px;
}
.pBtn a {
    width: 130px;
    height: 45px;
    background-color: rgb(102, 153, 0);
    display: block;
    text-align: center;
    color: white;
    font-family: 微软雅黑;
    font-size: 18px;
    line-height: 45px;
    position: relative;
    cursor: pointer;
    border-radius: 2px;
    padding: 0px 0px 0px 30px;
}
.pBtn a b {
    position: absolute;
    top: 12px;
    left: 18px;
    width: 22px;
    height: 20px;
    background: url(../img/productinfo.png) no-repeat -269px -72px;
    display: block;
}
.quickBuy a {
    padding: 0;
    background-color: #fa8e19;
}
