/* 预计发货时间 */
.phone_client {
    cursor: pointer;
    position: relative;
    float: left;
    width: 90px;
    height: 40px;
    border: 1px solid #ddd;
    background: #fbfbfb url(../img/phone.jpg) 10px center no-repeat;
    padding-left: 34px;
    padding-top: 3px;
    color: #565656;
}
/* 商品详情图片部分 */
.pView {
    float: left;
    width: 410px;
    position: relative;
    background-color: white;
    height: 410px;
    z-index: 92;
}
#pView {
    left: 10px;
    position: absolute;
    top: 20px;
    z-index: 100;
    width: 390px;
    margin-bottom: 20px;
}
#zoom-jpg {
    padding: 0;
    position: relative;
    float: right;
    height: 330px;
    width: 330px;
}
#zoom-jpg img{
    width: 330px;
    height: 330px;
}
.jqZoomLens { //小放div
    background-color: #fff;
    cursor: move;
    // height: 50px;
    opacity: 0.5;
    position: absolute;
    left: 0;
    top: 0;
    visibility: hidden;
    // width: 50px;
    z-index: 92;
}
.zoomdiv { //大放div
    background: url(../img/loading.gif) no-repeat center center #FFFFFF;
    border: 1px solid #E4E4E4;
    display: none;
    height: 410px;
    left: 400px;
    overflow: hidden;
    position: absolute;
    text-align: center;
    top: 0;
    width: 410px;
    z-index: 8;
}
.bigimg { //大放大图尺寸
    height: 800px;
    width: 800px;
    position: absolute;
}
/* --左边小图--- */
#pic-list {
    height: 300px;
    overflow: hidden;
    position: relative;
    width: 50px;
    margin-right: 10px;
    float: left;
    padding: 20px 0;
}
#pic-list .pic-items {
    left: 0;
    position: absolute;
    top: 20px;
}
#pic-list .pic-items img.img-hover {
    border: 1px solid #69af05;
    padding: 0;
}
// =========
#pView #pic-list a.disabled {
    cursor: default;
}
#btn-forward .disabled{
    background: url(../img/productinfo.png) no-repeat -30px 0;
}
#btn-bacward .disabled{
    background: url(../img/productinfo.png) no-repeat -30px -10px;
}
#pic-list .btn-control {
    display: block;
    height: 20px;
    position: absolute;
    left: 0;
    width: 50px;
    cursor: pointer;
}
#btn-forward {
    top: 0;
}
#btn-bacward {
    bottom: 0;
}
.btn-control b {
    top: 0;
    display: block;
    position: absolute;
    width: 19px;
    height: 10px;
    left: 14px;
    overflow: hidden;
}
#btn-forward b {
    background: url(../img/productinfo.png) no-repeat -49px 0;
}
#btn-bacward b {
    background: url(../img/productinfo.png) no-repeat -49px -10px;
}
#pic-list .pic-items li {
    font-size: 0;
    position: relative;
    text-align: center;
    width: 50px;
    line-height: 0;
    height: 60px;
    float: left;
}
#pic-list .pic-items img.img-hover {
    border: 1px solid #69af05;
    padding: 0;
}
#pic-list .pic-items img {
    height: 48px;
    padding: 1px;
    width: 48px;
}
/* share */
.pShare {
    position: absolute;
    top: 370px;
    border-top: 1px solid #efefef;
    width: 390px;
    margin: 0 10px;
}
.bdShare {
    padding: 5px 0 0 0;
    margin-bottom: 5px;
