}
/* 收藏商品 */
.pCollect {
    float: right;
    padding: 8px 0 0 20px;
    color: #6c9c0a;
    cursor: pointer;
    background: url(../img/productinfo.png) no-repeat -68px 11px;
    height: 20px;
}
.pCollect a {
    color: #6c9c0a;
}
/* 收藏商品end */

/* 右边 pItemsSide*/
.pItemsSide {
    float: right;
    width: 222px;
    overflow: hidden;
    background-color: #ffffff;
    position: relative;
    z-index: 1;
}
.points {
    padding-top: 20px;
    margin: 0 10px;
    overflow: hidden;
}
.points ul {
    width: 204px;
    border-bottom: 1px dashed #e8e8e8;
    overflow: hidden;
}
.points ul li {
    float: left;
    width: 67px;
    border-right: 1px dashed #e8e8e8;
    height: 65px;
    text-align: center;
}
.hcBox {
    margin: 0 10px;
    border-bottom: 1px solid #efefef;
    padding: 10px;
}
.hcBox .hcImg {
    height: 32px;
    padding-top: 10px;
    margin: 0 auto;
}
.hcBox .hcImg, .hcBox span {
    display: block;
}
.hcBox span {
    margin: 0 auto;
    line-height: 18px;
}
.hcBox .hcTitle {
    white-space: nowrap;
    text-overflow: ellipsis;
    height: 18px;
    padding-top: 6px;
    color: #659a02;
    font-size: 14px;
    text-align: center;
    overflow: hidden;
}
.hcBox .hcCon {
    padding-top: 10px;
    color: #646464;
}
.pdetail {
    padding: 10px 5px 10px 10px;
    margin: 0 10px;
    border-top: 1px solid #efefef;
}
.pdetail ul li {
    height: 16px;
    line-height: 16px;
    margin-bottom: 8px;
    overflow: hidden;
}
.nosupport {
    height: 23px;
    border: 1px solid #e3e3e3;
    border-radius: 10px;
    position: relative;
    padding: 0 0 0 23px;
    line-height: 23px;
    background-color: #fbfbfb;
    color: #969696;
}
.nosupport b {
    position: absolute;
    width: 13px;
    height: 14px;
    background: url(../img/productinfo.png) no-repeat -173px -28px;
    top: 5px;
    left: 5px;
}
.pcommdetail {
    padding-bottom: 10px;
    border-top: 1px solid #efefef;
}
.prate {
    padding: 5px 10px 0 20px;
    overflow: hidden;
    zoom: 1;
}
.prate dt {
    font-weight: bold;
    float: left;
    line-height: 30px;
}
.prate dd {
    display: block;
    overflow: hidden;
}
.prate dd .dd {
    float: left;
    width: 80px;
    height: 8px;
    background-color: #f9b872;
    margin: 12px 5px 0 5px;
    overflow: hidden;
}
.prate dd .dd span {
    height: 8px;
    background-color: #f9a345;
    display: block;
    overflow: hidden;
}
.prate dd strong {
    font-size: 24px;
    font-family: 微软雅黑;
    color: rgb(250, 130, 0);
    zoom: 1;
    line-height: 30px;
    overflow: hidden;
}
.prate dd strong span {
    font-size: 14px;
}
.pcomment {
    padding: 0 20px;
}
.pcomment dt {
    color: #6c9c0a;
}
.pcomment dd {
    color: #999999;
    padding-top: 6px;
    position: relative;
}
.pcomment dd b {
    position: absolute;
    top: 0;
    border-color: #FBFBFB #FBFBFB #F8CD9E #FBFBFB;
    border-style: dashed dashed solid dashed;
    border-width: 0 5px 6px 5px;
    display: inline-block;
    font-size: 0;
    height: 0;
    overflow: hidden;
    width: 0;
    left: 30px;
}
.pcomment dd div {
    border: 1px solid #f8cd9e;
    padding: 8px 10px;
}
.pcomment dd div span {
    display: block;
    line-height: 20px;
    height: 60px;
    overflow: hidden;
    word-break: break-all;
}
.pcomment dd div span a:link, .pcomment dd div span a:visited {
    color: #999999;
}
